@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700,800|Roboto:400,900);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700,800|Roboto:400,900);
nav {
  position: fixed;
  z-index: 8888;
  top: -100%;
  width: 100%;
  height: 100%;
  background-color: #262626;
  border-bottom: 4px solid #1E90FF;
  transition: top 0.5s; }
  nav:focus-within {
    top: 0; }

.nav-show {
  top: 0;
  width: 100%;
  height: 100%; }

.nav-align-wrapper {
  height: 100%;
  -webkit-transform: translate(0, 50%);
          transform: translate(0, 50%);
  pointer-events: none; }

.nav-aligner {
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  pointer-events: initial; }

.nav-link {
  display: -webkit-flex;
  display: flex;
  max-width: 600px;
  height: 100px;
  margin: 20px auto;
  font-family: "Open Sans", "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: white;
  text-decoration: unset;
  background-color: #222222;
  border-bottom: 6px solid transparent; }
  .nav-link:hover {
    color: #1E90FF;
    border-bottom: 6px solid #1E90FF; }

.nav-link.active {
  border-bottom: 6px solid #1E90FF; }

.nav-link span {
  margin: auto; }

.nav-toggle {
  position: fixed;
  top: 30px;
  right: 30px;
  z-index: 9999;
  width: 60px;
  height: 60px;
  padding: unset;
  background-color: #222222;
  border: 2px solid #1E90FF;
  border-radius: 50%;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  transition: top 0.5s, right 0.5s, -webkit-transform 1s;
  transition: top 0.5s, right 0.5s, transform 1s;
  transition: top 0.5s, right 0.5s, transform 1s, -webkit-transform 1s; }
  .nav-toggle:hover {
    color: #1E90FF;
    cursor: pointer; }
    .nav-toggle:hover .menu-icon, .nav-toggle:hover .close-icon {
      fill: #1E90FF; }
  .nav-toggle:focus {
    outline: 1px dotted gray; }

@media screen and (max-width: 768px) {
  .nav-toggle {
    top: calc(100%);
    right: calc(50%);
    -webkit-transform: translate(30px, -90px);
            transform: translate(30px, -90px); } }

.menu-icon, .close-icon {
  fill: white;
  margin: 5px; }

* {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  height: 100%;
  margin: unset;
  font-family: "Open Sans", "Roboto", sans-serif;
  color: white;
  background-color: #202020; }

#root {
  height: 100%; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: unset;
  font-weight: bold; }

h1 {
  font-size: 2em; }

h2 {
  font-size: 1.5em; }

h3 {
  font-size: 1.17em; }

h4 {
  font-size: 1em; }

h5 {
  font-size: .83em; }

h6 {
  font-size: .67em; }

header {
  margin: auto;
  max-width: 1200px;
  background-color: #262626;
  border-bottom: 2px solid #1E90FF; }

main {
  max-width: 1200px;
  padding: 60px;
  margin: auto;
  background-color: #262626;
  transition: padding 0.5s; }

main > * + * {
  margin-top: 60px; }

@media screen and (max-width: 768px) {
  main {
    padding: 30px; }
  main > * + * {
    margin-top: 60px; } }

footer {
  max-width: 1200px;
  padding: 30px 60px 120px;
  margin: auto;
  text-align: center;
  background-color: #262626;
  border-top: 2px solid #1E90FF; }

.center {
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-self: center;
          align-self: center;
  text-align: center;
  margin: auto; }

.site-title {
  display: inline-block;
  margin: 30px 60px;
  font-family: "Open Sans", "Roboto", sans-serif;
  font-size: 38px;
  font-weight: 800;
  transition: font-size 0.5s, margin 0.5s; }

@media screen and (max-width: 768px) {
  .site-title {
    font-size: 29.23077px;
    margin: 20px 0 20px 30px; } }

.img {
  display: block;
  max-width: 100%;
  max-height: 300px;
  margin: 20px auto; }

.hidden {
  height: 0;
  font-size: 0;
  padding: 0;
  margin: 0; }

